::selection {
  background-color: #0000ff;
  color: #fff;
}

html {
  -webkit-tap-highlight-color: rgba(0, 0, 255, 0.5);
}

.test {
  margin: 20px 0;
}

.options__row,
.form__row {
  position: relative;
  clear: left;
}

.form__row--inline input {
  display: inline-block;
  width: auto;
}

.label--block {
  display: block;
  float: none;
  text-align: left;
  width: auto;
  margin-bottom: 5px;
}

.label--inline {
  display: inline-block;
  float: none;
  text-align: left;
  width: auto;
  line-height: 1.5;
}

.fieldset {
  margin: 0;
  padding: 0;
  border: 0;
  border: 1px solid #333;
  padding: 10px;
}
.fieldset legend {
  padding: 0 10px;
}

.fieldset--invisible {
  margin: 0;
  padding: 0;
  border: 0;
}
.fieldset--invisible legend {
  position: absolute !important;
  clip: rect(1px, 1px, 1px, 1px);
  padding: 0 !important;
  border: 0 !important;
  height: 1px !important;
  width: 1px !important;
  overflow: hidden;
}

.input--wide {
  display: block;
  width: 100%;
}

.input--inline {
  display: inline-block;
  width: auto;
}

.input--text {
  margin: 0;
  max-width: 100%;
  vertical-align: middle;
  font-family: inherit;
  font-size: inherit;
  appearance: none;
  padding: 5px 10px;
  border: 1px solid #333;
  border-radius: 0;
  color: #333;
}
.input--text::-ms-clear {
  display: none;
}
.input--text::placeholder {
  opacity: 1;
  font-style: normal;
}

.input--textarea {
  margin: 0;
  max-width: 100%;
  vertical-align: middle;
  font-family: inherit;
  font-size: inherit;
  appearance: none;
  padding: 5px 10px;
  border: 1px solid #333;
  border-radius: 0;
  color: #333;
  min-height: 5em;
  overflow: auto;
  vertical-align: top;
  resize: vertical;
}
.input--textarea::-ms-clear {
  display: none;
}
.input--textarea::placeholder {
  opacity: 1;
  font-style: normal;
}

.input--checkbox {
  position: absolute;
  left: -9999px;
}
.input--checkbox + label {
  cursor: pointer;
  position: relative;
  padding-left: 24px;
}
.input--checkbox + label:before {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  width: 20px;
  height: 20px;
  border: 1px solid #333;
  background: #fff;
}
.input--checkbox + label:hover:before {
  background-color: #ecebeb;
}
.input--checkbox + label:hover:after {
  background-color: #0000e6;
}
.input--checkbox + label:after {
  content: "";
  width: 14px;
  height: 14px;
  background: #333;
  position: absolute;
  top: 3px;
  left: 3px;
  line-height: 1.2;
  animation-name: check;
  animation-duration: 150ms;
  animation-timing-function: ease-out;
  animation-direction: normal;
  animation-iteration-count: 1;
  animation-fill-mode: forwards;
}
.input--checkbox:focus + label:before {
  background-color: #ecebeb;
  outline: 1px dashed #0000ff;
  outline-offset: 2px;
}
.input--checkbox:focus + label:after {
  background-color: #0000e6;
}
.input--checkbox:not(:checked) + label:after {
  display: none;
}
.input--checkbox:checked + label:after {
  display: block;
}
.input--checkbox[disabled] + label {
  color: #ccc;
}
.input--checkbox[disabled] + label:before {
  background-color: #aaa;
}
.input--checkbox[disabled] + label:after {
  color: #eee;
  background-color: #ddd;
}
.input--checkbox + label.checkbox-right {
  padding-left: 0;
  padding-right: 20px;
}
.input--checkbox + label.checkbox-right:before {
  left: auto;
  right: 0;
}
.input--checkbox + label.checkbox-right:after {
  left: auto;
  right: 2px;
}
.input--checkbox + label.label-hidden {
  margin-right: 0;
}
.input--checkbox + label.label-hidden span {
  position: absolute !important;
  clip: rect(1px, 1px, 1px, 1px);
  padding: 0 !important;
  border: 0 !important;
  height: 1px !important;
  width: 1px !important;
  overflow: hidden;
}
@keyframes check {
  from {
    transform: scale(0);
    border-radius: 50%;
  }
  50% {
    transform: scale(1.1);
    border-radius: 50%;
  }
  to {
    transform: scale(1);
    border-radius: 2px;
  }
}

